.profile__subtitle {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
    padding: 0;

    color: #FFFFFF;
    
    text-overflow: ellipsis;
    max-width: 294px;
    overflow: hidden;
    white-space: nowrap; 
}

@media screen and (max-width: 620px) {
    .profile__subtitle {
        font-size: 14px;
        line-height: 17px;
        text-align: center;
    }
}